body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.chart {
  background: white;
  width: 300px;
  height: 50px;
  border-left: 1px dotted #555;
  border-bottom: 1px dotted #555;
}

circle {
  cursor: pointer;
}

line {
  cursor: move;
}

svg {
  padding: 5px;
  margin-bottom: 10px;
}

span {
  background: white;
}



